
.post {
    font-size: 12px;
    background-color: #B5E51C;
    width: 390px;
    height: 90px;
    display: inline-block;

}

.message {
    width: 300px;
    height: 80px;
    background-color: #082103;
    float: right;
    margin-top: 5px;
    margin-right: 10px;
    position: relative;
    border-radius: .4em .4em .4em 0;
    display: grid;
    grid-template-areas:
    "name name"
    "text time";

}

.message:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 0;
    border: 0.469em solid transparent;
    border-right-color: #082103;
    border-left: 0;
    border-bottom: 0;
    margin-top: -0.234em;
    margin-left: -0.469em;
}


.post img {
    width: 50px;
    height: 50px;
    margin-left: 15px;
    margin-top: 30px;
    border-radius: 50%;

}



.name {
    color: #927BAF;
    grid-area: name;
    margin: 20px 0 0 20px;
}

.text {
    color: #ffffff;
    grid-area: text;
    margin: 0 0 20px 20px;
}

.time {
    color: #344D30;
    grid-area: time;
    margin: 20px 20px 0px 0;
    text-align: right;

}
.right{
    position: relative;
    float: right;
    display: flex;
    flex-direction: row-reverse;
    grid-template-areas:
        "name name"
        "time text";
}
.right .message{
    position: relative;
    justify-content: end;

}
.right .name{

    padding-right: 1rem;
}
.right .time{
    display: flex;
justify-content: start;
    margin-left: -17rem;
}
.right .message:after {
    content: '';
    position: absolute;
    left: 300px;
    bottom: 0px;
    width: 0;
    height: 0;
    border: 0.469em solid transparent;
    border-bottom-color: #082103;
    margin-bottom: 0px;
    margin-right: -8px;
}